<template>
  <div class="home animated fadeIn" :style="home_height">
    <div class="img-title">
      <img src="../assets/top_bottom.png"/>
      <div class="font">
        滚动说明优惠力度以每次购买个数为准，购买历史不重复叠加，建议需要购买
      </div>
    </div>

    <div class="content-template">
      <div class="left-template">
        <div class="bar" v-for="(li, index) in tag" :key="index" :class="{ 'active-tag': li.active }">
          <a href="" @click.prevent="custormAnchor(li.tagName, index)">{{ li.tagName }}</a>
        </div>
      </div>

      <div ref="right-template" @scroll="onScroll" class="right-template">
        <div v-for="(li, index) in tag" :key="index">
          <div v-if="li.tagName === '优惠说明'">
            <div class="bar" :id="li.tagName">{{ li.tagName }}</div>
            <div class="yhsm">
              1.活动时间：即日起至-2019年7月19日<br/><br/>
              2.优惠说明：<br/><br/>
              <div class="table">
                <div class="title">单次购买≥31个SQ音频</div><div class="price">5折</div>
                <div class="title">21个≤单次购买SQ音频≤30个</div><div class="price">6折</div>
                <div class="title">10个≤单次购买SQ音频≤20个</div><div class="price">7折</div>
                <div class="title">单次购买SQ音频≤9个</div><div class="price">8折</div>
              </div><br/>
              3.享受优惠以单次购买个数为准，购买历史不重复叠加，如有需要，建议一次性选购更多内容，获取更大优惠。<br/><br/>
              4.购买成功后，请前往氛围音页面体验，具体如下：点击中间圆圈-“VIP专属”-“已购买音频右上角一个蓝色小锁头”-点击即可播放。<br/><br/>
              5.购买历史查询，请前往：“我的”-“账户充值”-“内容购买”。<br/><br/>
              6.小睡眠拥有最终解释权。<br/><br/>
            </div>
          </div>

          <div v-else>
            <div class="bar" :id="li.tagName">{{ li.tagName }}</div>
            <div class="goods" v-for="(li2, index2) in li.tagContent" :key="index2">
              <div class="title">{{ li2.title }}</div>
              <div class="price">¥ {{ li2.price }}</div>
              <div class="type">
                <img src="../assets/type/add.png" @click="add(index, index2, $event)" v-if="li2.type === 0"/>
                <img src="../assets/type/minus.png" @click="minus(index, index2, $event)" v-if="li2.type === 1"/>
                <img class="have" src="../assets/type/have.png"  v-if="li2.type === 2"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="goods-list-template" @click.self="carsShow = false" v-if="carsShow">
      <div class="goods-list">
        <div class="top">
          <p>购物车</p>
          <p @click="clearAll">清空</p>
        </div>
        <div class="goods-scroll">
          <div class="goods" v-for="(li, index) in goodsCarData.values()" :key="index">
            <div class="left">{{ li.title }}</div>
            <div class="right">¥ {{ li.price }}</div>
            <img class="minus" @click="_minus(li.id, $event)" src="../assets/type/minus.png"/>
          </div>
        </div>
      </div>
    </div>

    <div class="goods-car" :class="{ carsOpatity: goodsCount === 0 }">
      <div class="left" @click.self="carsShow = true">
        <div class="icon-car-template">
          <div class="count">{{ goodsCount }}</div>
          <img class="icon-car" src="../assets/icon_car_none.png" v-if="goodsCount === 0" />
          <img class="icon-car" src="../assets/icon-car.png" v-else />
        </div>

        <div class="number-template">
          <div class="top">
            <div>
              ¥ {{ realPrice }}
            </div>
            <div class="striping" style="margin-left: 10%" v-show="discount !== 10">
              <img src="../assets/striping.png"/>
              <p style="opacity: 0.3">¥ {{ goodsPrice }}</p>
            </div>
          </div>

          <div class="bottom">
            <p v-if="discount !== 10">优惠力度{{discount}}折，省{{ (goodsPrice - realPrice).toFixed(2) }}</p>
            <p v-else>限时低至5折</p>
          </div>
        </div>
      </div>

      <div class="right" @click="_buy" :class="{ carsOpatity: goodsCount === 0 }">确认购买</div>
    </div>

    <div class="tips-template" v-if="tipsShow">
      <div class="tips">
        <img class="close" @click="showTips($event)" src="../assets/close.png" />
        <img class="bg" v-if="successType === 0" src="../assets/fail.png"/>
        <img class="bg" v-else src="../assets/success.png"/>
        <p class="content">{{ tipsContent }}</p>
        <div class="button" @click="buttonEvent(buttonContent)" :class="{ bgRed: buttonContent === '确认购买' }">
          {{ buttonContent }}
        </div>
      </div>
    </div>

    <div style="position: absolute; opacity: 0;width: 1px;height:0px">
      <img src="../assets/success.png"/>
      <img src="../assets/fail.png"/>
    </div>

    <div class="select-all" @click="selectAllMethod">
      <img class="select-all-img" v-if="!selectAll" src="../assets/addAll.png">
      <img class="select-all-img" v-else src="../assets/addAllSelect.png">
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import { callAppRouter } from '@/js/tool.js'

export default {
  name: 'home',
  data () {
    return {
      shellCount: 0, // 用户的水杯
      selectAll: false,
      carsShow: false,
      goodsCarData: new Set(),
      tipsShow: false,
      buttonContent: '',
      tipsContent: '',
      successType: 3, // 0为失败 1为成功
      goodsCount: 0, // 用户选中物品的数量
      goodsPrice: 0, // 用户选中物品的价格总和
      discount: 10, // 优惠力度
      goodsId: new Set(), // 用户选中的id集合
      realPrice: 0, // 真实价格
      top: 0, // scroll title 的高度
      home_height: 0,
      tag: [{
        tagName: '学生考试必备',
        active: false,
        tagContent: []
      }],
      openid: '',
      redirect_interval: ''
    }
  },
  components: {
  },
  methods: {
    buttonEvent (content) {
      if (content === '确认购买') {
        this.successType = 1
        this.buy()
        return
      }
      if (content === '马上充值') {
        if (!this.openid) {
          this.getOpenid(true)
          return
        }
        // 跳转
        callAppRouter('Redirect', { code: '10025' }, (res, ed) => {})
        // console.log('去充值')
        return
      }
      if (content === '马上体验') {
        callAppRouter('Redirect', { code: '1026' }, (res, ed) => {})
      }
      this.tipsShow = false
    },
    showTips (e) {
      e.preventDefault()
      this.tipsShow = false
      // this.goodsCarData = new Set()
      // this.successType = 3 // 0为失败 1为成功
      // this.goodsCount = 0 // 用户选中物品的数量
      // this.goodsPrice = 0 // 用户选中物品的价格总和
      // this.discount = 10 // 优惠力度
      // this.goodsId = new Set() // 用户选中的id集合
      // this.realPrice = 0 // 真实价格
      // this.tag[0].tagContent = []
      // this.selectAll = false
      // this._getSourceData()
    },
    getLogin () {
      callAppRouter('isLogin', {}, (res, ed) => {
        console.log('??')
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        if (mesg.msg === '已登录') {
          this.getOpenid()
        } else {
          this._getSourceData()
        }
      })
    },
    getOpenid (reflash = false) {
      callAppRouter('Login', {}, (res, ed) => {
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        console.log(mesg, 'mesg')
        this.shellCount = mesg.data.shell_count_recharge
        this.openid = mesg.data.openid
        reflash === false ? this.tag[0].tagContent = [] : ''
        this._getSourceData(reflash)
        console.log(this.openid)
      })
    },
    // 设置优惠力度
    setDiscount (addAllMode = false) {
      const length = [...this.goodsId.values()].length
      this.goodsCount = length
      switch (true) {
        case length === 0:
          this.discount = 10
          break
        case length <= 9 && length > 0:
          this.discount = 8
          break
        case length <= 20 && length > 9:
          this.discount = 7
          break
        case length <= 30 && length > 20:
          this.discount = 6
          break
        case length >= 31:
          this.discount = 5
          break
      }
      if (addAllMode) {
        return
      }
      this.realPrice = ((this.goodsPrice * this.discount) / 10).toFixed(2)
    },
    add (index, index2, event = null, addAllMode = false) {
      // eslint-disable-next-line
      event ? event.preventDefault() : ''
      const data = this.tag[index].tagContent[index2]
      data.type = 1
      this.goodsId.add(data.id)
      this.goodsCarData.add(data)
      this.goodsPrice += +data.price
      this.goodsPrice = +this.goodsPrice.toFixed(2)
      this.setDiscount(addAllMode)
    },
    _add (id, event = null, addAllMode = false) {
      // eslint-disable-next-line
      event ? event.preventDefault() : ''
      this.tag.forEach((v, index) => {
        if (!v.hasOwnProperty('tagContent')) {
          return
        }
        for (let i = 0; i < v.tagContent.length; i++) {
          if (v.tagContent[i].id === id && v.tagContent[i].type === 0) {
            this.add(index, i, null, addAllMode)
          }
        }
      })
    },
    _minus (id, event = null, addAllMode = false) {
      // eslint-disable-next-line
      event ? event.preventDefault() : ''
      this.tag.forEach((v, index) => {
        if (!v.hasOwnProperty('tagContent')) {
          return
        }
        for (let i = 0; i < v.tagContent.length; i++) {
          if (v.tagContent[i].id === id && v.tagContent[i].type === 1) {
            this.minus(index, i, null, addAllMode)
          }
        }
      })
    },
    // 清空按钮
    clearAll (addAllMode = false) {
      for (let i of this.goodsCarData.values()) {
        this._minus(i.id, null, addAllMode)
      }
      this.selectAll = false
    },
    selectAllMethod () {
      if (this.selectAll === false) {
        this.selectAll = true
        this.tag.forEach(v => {
          if (!v.hasOwnProperty('tagContent')) {
            return
          }
          for (let i of v.tagContent) {
            this._add(i.id, null, true)
          }
          this.realPrice = ((this.goodsPrice * this.discount) / 10).toFixed(2)
        })
        // this.realPrice = +((this.goodsPrice * this.discount) / 10).toFixed(2)
      } else {
        this.selectAll = false
        this.clearAll(true)
        this.realPrice = ((this.goodsPrice * this.discount) / 10).toFixed(2)
      }
    },
    minus (index, index2, event, addAllMode = false) {
      // eslint-disable-next-line
      event ? event.preventDefault() : ''
      const data = this.tag[index].tagContent[index2]
      data.type = 0
      this.goodsPrice -= +data.price
      this.goodsPrice = +this.goodsPrice.toFixed(2)
      this.goodsId.delete(data.id)
      this.goodsCarData.delete(data)
      this.setDiscount(addAllMode)
    },
    onScroll () {
      let done = false
      for (let i of this.tag) {
        if (done) {
          return
        }
        const offsetTop = document.querySelector(`#${i.tagName}`).offsetTop
        const scrollTop = document.querySelector('.right-template').scrollTop
        if ((offsetTop - scrollTop) < 50 && (offsetTop - scrollTop) > -50) {
          for (let i2 of this.tag) {
            i2.active = false
            if (i2.tagName === i.tagName) {
              done = true
              i2.active = true
            }
          }
        }
      }
    },
    custormAnchor (value, index) {
      for (let i of this.tag) {
        i.active = false
      }
      this.tag[index].active = true
      let anchorElement = document.getElementById(value)
      if (anchorElement) {
        let total = anchorElement.offsetTop
        // Chrome
        document.querySelector('.right-template').scrollTop = total
        this.clickBusy = false
        // Firefox
        // document.documentElement.scrollTop = total
        // // Safari
        // window.pageYOffset = total
      }
    },
    // 获取对应的商品资源id
    getSourceData (p = 0, reflash = false) {
      return new Promise((resolve) => {
        this.$axios.get('/web/v1/cosleep/coupon/sq', {
          params: { p },
          headers: { 'openid': this.openid || '' }
        }).then(res => {
          const data = res.data.data
          for (let i of data) {
            const _data = {
              id: i.id,
              price: i.price,
              title: i.musicdesc,
              type: +i.have_auth === 1 ? 2 : 0 // 0 还没有加入购物车  1 已经加入购物车  2 已经拥有了
            }
            if (reflash) {
              /* for (let i2 of this.tag[0].tagContent) {
                if (i2.id === i.id) {
                  i2.type = +i.have_auth === 1 ? 2 : 0 // 0 还没有加入购物车  1 已经加入购物车  2 已经拥有了
                } else {
                  console.log('不想打')
                }
              } */
            } else {
              this.tag[0].tagContent.push(_data)
            }
          }
          resolve(data)
        })
      })
    },
    async _getSourceData (reflash = false) {
      this.$root.showLoading = true
      let page = 0
      while (true) {
        const data = await this.getSourceData(page, reflash)
        if (data.length === 0) {
          this.$root.showLoading = false
          return
        }
        page += 1
      }
    },
    _buy () {
      console.log(this.goodsCount)
      if (this.goodsCount === 0) {
        return
      }

      this.$root.showLoading = true

      callAppRouter('Redirect', { code: '10020' }, (res, ed) => {
      })
      setTimeout(() => {
        this.getOpenid(true)
        console.log(
          this.shellCount
        )
        if (this.shellCount < this.realPrice || this.shellCount === 0) {
          this.tipsShow = true
          this.successType = 0
          this.tipsContent = `支付睡呗${this.realPrice}`
          this.buttonContent = '马上充值'
        } else {
          this.tipsShow = true
          this.successType = 1
          this.buttonContent = '确认购买'
          this.tipsContent = `支付睡呗${this.realPrice}`
        }
        this.$root.showLoading = false
      }, 300)
    },
    buy () {
      if (!this.openid) {
        this.getOpenid()
        return
      }

      this.$axios({
        url: '/web/v1/cosleep/coupon/sq/buy',
        method: 'POST',
        data: {
          'music_ids': JSON.stringify([...this.goodsId.values()])
        },
        headers: {
          'openid': this.openid || ''
        }
      }).then(res => {
        const data = res.data
        if (data.status === 1) {
          // 成功后 需要刷新数据
          // this.clearAll() // 清空购物车
          setTimeout(() => {
            this.goodsCarData = new Set()
            this.successType = 3 // 0为失败 1为成功
            this.goodsCount = 0 // 用户选中物品的数量
            this.goodsPrice = 0 // 用户选中物品的价格总和
            this.discount = 10 // 优惠力度
            this.goodsId = new Set() // 用户选中的id集合
            this.realPrice = 0 // 真实价格
            this.tag[0].tagContent = []
            this._getSourceData()
          }, 0)
          callAppRouter('Redirect', { code: '10020', tag_id: 15 }, (res, ed) => {
          })
        }
        this.buttonContent = '马上体验'
        this.tipsContent = data.msg
        if (data.status === 1032) {
          this.successType = 0
          this.buttonContent = '马上充值'
        }
        // this.$root.showLoading = false
      })
    }
  },
  mounted () {
    this.getLogin()

    this.top = document.querySelector('.img-title').clientHeight

    this.onScroll()

    this.tag.push({
      tagName: '优惠说明',
      active: false
    })
  }
}
</script>

